.table-grid {
  display: flex;
  height: 100vh;
  background-color: #f9f9f9;
  overflow: scroll;
  max-width: 100%;
}

th{
  text-align: left;
  padding-left: 15px
}

select {
  padding: 0.5rem;
  padding-top: 1rem;
  width: 100%;
  margin: 10px 0;
}

.text {
  padding: 0.5rem;
  padding-top: 1rem;
  display: block;
  width: 97%;
  margin: 10px 0px;
}

.reverse-checkbox{
  display: flex;
  flex-direction: row-reverse;
  width: fit-content;
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .table-grid {
    flex-direction: column;
  }
}
.table-grid tr {
  text-align: left;
  cursor: pointer;
  font-size: 1rem;
}
.table-grid tr:hover {
  background-color: lightgray;
  transition: 100ms ease-out;
}
.table-grid tr td {
  padding: 1rem;
  width: 100px;
  text-align: left;
}
.table-grid-column-one {
  padding: 2rem;
  flex: 1;
  max-width: 100%;
  overflow: scroll;
}

.form-container {
  background: lightgray;
  flex: 1;
  padding: 0 1rem;
}


#summary {
  max-width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
